/* Mini Post */
.cell-container {
  position: relative;

  .description-alone {
    padding: 0;
    bottom: 0;
    border-top: solid 1px _palette(border);

    p {
      font-size: 0.9rem;
      margin: 0;
      padding: 1.25em;
      background-color: rgba(255, 255, 255, .7);
      color: #3c3b3b;
      font-family: "Raleway", Helvetica, sans-serif;
      z-index: 1000;
      transition: all 0.1s;
      -webkit-transition: all 0.1s ease-in-out;
    }
  }

  .description {
    position: absolute;
    padding: 0;
    bottom: 0;
    border-top: solid 1px _palette(border);

    p {
      font-size: 0.9rem;
      margin: 0;
      padding: 1.25em;
      background-color: rgba(255, 255, 255, .7);
      color: #3c3b3b;
      font-family: "Raleway", Helvetica, sans-serif;
      z-index: 1000;
      transition: all 0.1s;
      -webkit-transition: all 0.1s ease-in-out;
    }

    p:hover {
      background-color: rgba(255, 255, 255, .9);
    }
  }

  @media (max-width: 480px) {
    .description {
      position: inherit;
      padding: 0;
      bottom: inherit;
    }
  }
}

.link-to-project {
  color: black;
}

.mini-post {
  @include vendor('display', 'flex');
  @include vendor('flex-direction', 'column');
  background: _palette(bg);
  border: solid 1px _palette(border);
  margin: 0 0 _size(element-margin);

  .image-hidden {
    display: none;
  }

  header {
    z-index: 2;
    @include padding(1.25em, 1.25em, (0, 3em, 0, 0));
    min-height: 4em;
    position: relative;
    @include vendor('flex-grow', '1');

    h3 {
      font-size: 0.7em;
    }

    .published {
      display: block;
      font-family: _font(family-heading);
      font-size: 0.6em;
      font-weight: _font(weight-heading);
      letter-spacing: _font(kerning-heading);
      margin: -0.625em 0 (_size(element-margin) * 0.25) 0;
      text-transform: uppercase;
    }

    .author {
      position: absolute;
      right: 2em;
      top: 2em;
    }
  }
}

.mini-posts {
  margin: 0 0 _size(element-margin);
  @include breakpoint(large) {
    @include vendor('display', 'flex');
    @include vendor('flex-wrap', 'wrap');
    width: calc(100% + #{_size(element-margin)});

    > * {
      margin: _size(element-margin) _size(element-margin) 0 0;
      width: calc(50% - #{_size(element-margin)});
    }

    > :nth-child(-n + 2) {
      margin-top: 0;
    }
  }
  @include breakpoint(xsmall) {
    display: block;
    width: 100%;

    > * {
      margin: 0 0 _size(element-margin);
      width: 100%;
    }
  }
}

.gallery {
  padding: 1em 0vw;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  overflow-x: visible;
}

.gallery > div {
  width: 5em;
  height: 5em;
}

.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  &:hover {
    transform: scale(1.1);
  }
}

.gallery-img-clicked {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  z-index: 9999;
  -webkit-box-shadow: 10px 10px 19px -6px rgba(0,0,0,0.26);
  -moz-box-shadow: 10px 10px 19px -6px rgba(0,0,0,0.26);
  box-shadow: 10px 10px 19px -6px rgba(0,0,0,0.26);
}